import React from 'react';
import Card2 from './Card2';
import Arrow from './Arrow';
import { robots } from '../containers/robots';

const BigCard= ()=>{
    return(
        <div className = "About__Page tc">
            <Card2 
            key = {robots[0].key} 
            name={robots[0].name} 
            id = {robots[0].id} 
            email={robots[0].email} 
            />
            <Arrow />
        </div>
    );
}
export default BigCard