import React, { Component } from 'react';
import {Link } from "react-router-dom";
class Card extends Component{
    render(){
        return(
			<Link to='/About' className = "col-1-of-4" >
			    <div className = "card mrg-top500 bg-light-blue card__design  grow">
				    <img className = "card card__photo" src = {`https://robohash.org/${this.props.id}`} alt = 'robo img'/>
					<div className = "card card__details u-center-text">
					    <h2>{this.props.name}</h2>
					    <p>{this.props.email}</p>
					</div>
				</div>
			</Link>          
        );
    }
}
export default Card